referred-font($usePreferred);\r\n  font-size: 56px;\r\n  font-weight: 400;\r\n  line-height: 1.35;\r\n  letter-spacing: -0.02em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-display-2($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 45px;\r\n  font-weight: 400;\r\n  line-height: 48px;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-display-1($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 34px;\r\n  font-weight: 400;\r\n  line-height: 40px;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-headline($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 24px;\r\n  font-weight: 400;\r\n  line-height: 32px;\r\n  -moz-osx-font-smoothing: grayscale;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-title($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 20px;\r\n  font-weight: 500;\r\n  line-height: 1;\r\n  letter-spacing: 0.02em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-subhead($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 16px;\r\n  font-weight: 400;\r\n  line-height: 24px;\r\n  letter-spacing: 0.04em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-subhead-2($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 16px;\r\n  font-weight: 400;\r\n  line-height: 28px;\r\n  letter-spacing: 0.04em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-body-2($colorContrast: false, $usePreferred: false) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  @if $usePreferred {\r\n  font-weight: 500;\r\n  } @else {\r\n  font-weight: bold;\r\n  }\r\n  line-height: 24px;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-body-1($colorContrast: false, $usePreferred: false) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  font-weight: 400;\r\n  line-height: 24px;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-caption($colorContrast: false, $usePreferred: false) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 12px;\r\n  font-weight: 400;\r\n  line-height: 1;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-blockquote($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  position: relative;\r\n  font-size: 24px;\r\n  font-weight: 300;\r\n  font-style: italic;\r\n  line-height: 1.35;\r\n  letter-spacing: 0.08em;\r\n\r\n  &:before {\r\n  position: absolute;\r\n  left: -0.5em;\r\n  content: '';\r\n  }\r\n\r\n  &:after {\r\n  content: '';\r\n  margin-left: -0.05em;\r\n  }\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-menu($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  line-height: 1;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-button($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  text-transform: uppercase;\r\n  line-height: 1;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-icon() {\r\n  font-family: 'Material Icons';\r\n  font-weight: normal;\r\n  font-style: normal;\r\n  font-size: 24px;\r\n  line-height: 1;\r\n  letter-spacing: normal;\r\n  text-transform: none;\r\n  display: inline-block;\r\n  word-wrap: normal;\r\n  font-feature-settings: 'liga';\r\n  -webkit-font-feature-settings: 'liga';\r\n  -webkit-font-smoothing: antialiased;\r\n}\r\n\r\n/* Shadows */\r\n\r\n// Focus shadow mixin.\r\n@mixin focus-shadow() {\r\n  box-shadow: 0 0 8px rgba(0,0,0,.18),0 8px 16px rgba(0,0,0,.36);\r\n}\r\n\r\n@mixin shadow-2dp() {\r\n  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 3px 1px -2px rgba(0, 0, 0, $shadow-key-umbra-opacity),\r\n        0 1px 5px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);\r\n}\r\n@mixin shadow-3dp() {\r\n  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 3px 3px -2px rgba(0, 0, 0, $shadow-key-umbra-opacity),\r\n        0 1px 8px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);\r\n}\r\n@mixin shadow-4dp() {\r\n  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 1px 10px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 2px 4px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n@mixin shadow-6dp() {\r\n  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 1px 18px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 3px 5px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n@mixin shadow-8dp() {\r\n  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 3px 14px 2px rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 5px 5px -3px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n\r\n@mixin shadow-16dp() {\r\n  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0  6px 30px 5px rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0  8px 10px -5px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n\r\n@mixin shadow-24dp() {\r\n  box-shadow: 0  9px 46px  8px rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 11px 15px -7px rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 24px 38px  3px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n\r\n/* Animations */\r\n\r\n@mixin material-animation-fast-out-slow-in($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-fast-out-slow-in;\r\n}\r\n\r\n@mixin material-animation-linear-out-slow-in($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-linear-out-slow-in;\r\n}\r\n\r\n@mixin material-animation-fast-out-linear-in($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-fast-out-linear-in;\r\n}\r\n\r\n@mixin material-animation-default($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-default;\r\n}\r\n\r\n/* Dialog */\r\n\r\n@mixin dialog-width($units:5) {\r\n  @if(type_of($units) != 'number') {\r\n  @error \"The unit given to dialog-width should be a number.\";\r\n  }\r\n  // 56dp is the base unit width for Dialogs.\r\n  // With 5 units being the number of units for a mobile device.\r\n  // https://goo.gl/sK2O5o\r\n  width: $units * 56px;\r\n}\r\n","// note: no img assets required for layout\r\n\r\nhtml {\r\n\theight: 100%;\n}\r\n\r\nbody {\r\n\theight: 100%;\r\n\tmargin: 0;\r\n\tpadding: 0;\r\n\tbackground: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../assets/images/backgrounds/scala.jpg) fixed no-repeat;\r\n}\r\n\r\n.full-height{\r\n\theight: 100% !important;\r\n}\r\n\r\n.bg-color-medlight{\r\n\t~.app-page-container{\r\n\t\t.full-height{\r\n\t\t\tbackground: rgba(255, 255, 255, 0.64);\r\n\t\t}\r\n\t}\r\n}\r\n\r\n.article-title{\r\n\tpadding: 20px 25px;\r\n}\r\n\r\n.main-content{\r\n\tpadding: 0 25px;\r\n}\r\n","// layout\r\n.app-header{\r\n  display: block;\r\n  position: relative;\r\n  z-index: $l_zindex_navbar;\r\n  height: $l_header_height;\r\n  width: 100%;\r\n  background-color: $l_white;\r\n\r\n  .toggle-sidebar{\r\n    display: block;\r\n  }\r\n}\r\n\r\n.fixed-header {\r\n  .app-header {\r\n    position: fixed;\r\n    left: 0;\r\n    top: 0;\r\n    right: 0;\r\n  }\r\n}\r\n// desktop\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n  .app-header {\r\n    .toggle-sidebar-btn {\r\n      display: none;\r\n    }\r\n\r\n    .brand {\r\n      display: inline-block;\r\n      width: $l_sidebar_width;\r\n      padding: 0 0 0 $l_sidebar_width_collapsed;\r\n      transition: padding .25s $l_transition_ease_out;\r\n      a {\r\n        text-decoration: none;\r\n        font-weight: normal;\r\n      }\r\n    }\r\n  }\r\n\r\n  .nav-behind {\r\n    .app-header {\r\n      .brand {\r\n        padding-left: 0;\r\n      }\r\n    }\r\n  }\r\n}\r\n","// Variables\r\n//\r\n// Copy settings from this file into the provided `_custom.scss` to override\r\n// the Bootstrap defaults without modifying key, versioned files.\r\n\r\n\r\n// Table of Contents\r\n//\r\n// Colors\r\n// Options\r\n// Spacing\r\n// Body\r\n// Links\r\n// Grid breakpoints\r\n// Grid containers\r\n// Grid columns\r\n// Fonts\r\n// Components\r\n// Tables\r\n// Buttons\r\n// Forms\r\n// Dropdowns\r\n// Z-index master list\r\n// Navbar\r\n// Navs\r\n// Pagination\r\n// Jumbotron\r\n// Form states and alerts\r\n// Cards\r\n// Tooltips\r\n// Popovers\r\n// Badges\r\n// Modals\r\n// Alerts\r\n// Progress bars\r\n// List group\r\n// Image thumbnails\r\n// Figures\r\n// Breadcrumbs\r\n// Carousel\r\n// Close\r\n// Code\r\n\r\n@mixin _assert-ascending($map, $map-name) {\r\n  $prev-key: null;\r\n  $prev-num: null;\r\n  @each $key, $num in $map {\r\n    @if $prev-num == null {\r\n      // Do nothing\r\n    } @else if not comparable($prev-num, $num) {\r\n      @warn \"Potentially invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} whose unit makes it incomparable to #{$prev-num}, the value of the previous key '#{$prev-key}' !\";\r\n    } @else if $prev-num >= $num {\r\n      @warn \"Invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} which isn't greater than #{$prev-num}, the value of the previous key '#{$prev-key}' !\";\r\n    }\r\n    $prev-key: $key;\r\n    $prev-num: $num;\r\n  }\r\n}\r\n\r\n// Replace `$search` with `$replace` in `$string`\r\n// @author Hugo Giraudel\r\n// @param {String} $string - Initial string\r\n// @param {String} $search - Substring to replace\r\n// @param {String} $replace ('') - New value\r\n// @return {String} - Updated string\r\n@function str-replace($string, $search, $replace: \"\") {\r\n  $index: str-index($string, $search);\r\n\r\n  @if $index {\r\n    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);\r\n  }\r\n\r\n  @return $string;\r\n}\r\n\r\n@mixin _assert-starts-at-zero($map) {\r\n  $values: map-values($map);\r\n  $first-value: nth($values, 1);\r\n  @if $first-value != 0 {\r\n    @warn \"First breakpoint in `$grid-breakpoints` must start at 0, but starts at #{$first-value}.\";\r\n  }\r\n}\r\n\r\n\r\n// General variable structure\r\n//\r\n// Variable format should follow the `$component-modifier-state-property` order.\r\n\r\n\r\n// Colors\r\n//\r\n// Grayscale and brand colors for use across Bootstrap.\r\n\r\n// Start with assigning color names to specific hex values.\r\n$white:  #fff !default;\r\n$black:  #000 !default;\r\n$red:    #EF5350 !default; // Red 400\r\n$orange: #FFCA28 !default; // Amber 400\r\n$yellow: #ffd500 !default;\r\n$green:  #66BB6A !default; // Green 400\r\n$blue:   #2196F3 !default; // Blue 500\r\n$teal:   #5bc0de !default;\r\n$pink:   #ff5b77 !default;\r\n$purple: #7E57C2 !default; // Deep Purple 400\r\n\r\n// Create grayscale\r\n$gray-darker:               #222 !default;\r\n$gray-dark:                 #292b2c !default;\r\n$gray:                      #464a4c !default;\r\n$gray-light:                #636c72 !default;\r\n$gray-lighter:           