order-color:        rgba($black,.125) !default;\r\n$list-group-border-width:        $border-width !default;\r\n$list-group-border-radius:       $border-radius !default;\r\n\r\n$list-group-item-padding-x:      1.25rem !default;\r\n$list-group-item-padding-y:      .75rem !default;\r\n\r\n$list-group-hover-bg:            $gray-lightest !default;\r\n$list-group-active-color:        $component-active-color !default;\r\n$list-group-active-bg:           $component-active-bg !default;\r\n$list-group-active-border:       $list-group-active-bg !default;\r\n$list-group-active-text-color:   lighten($list-group-active-bg, 50%) !default;\r\n\r\n$list-group-disabled-color:      $gray-light !default;\r\n$list-group-disabled-bg:         $list-group-bg !default;\r\n$list-group-disabled-text-color: $list-group-disabled-color !default;\r\n\r\n$list-group-link-color:          $gray !default;\r\n$list-group-link-heading-color:  $gray-dark !default;\r\n$list-group-link-hover-color:    $list-group-link-color !default;\r\n\r\n$list-group-link-active-color:   $list-group-color !default;\r\n$list-group-link-active-bg:      $gray-lighter !default;\r\n\r\n\r\n// Image thumbnails\r\n\r\n$thumbnail-padding:           .25rem !default;\r\n$thumbnail-bg:                $body-bg !default;\r\n$thumbnail-border-width:      $border-width !default;\r\n$thumbnail-border-color:      #ddd !default;\r\n$thumbnail-border-radius:     $border-radius !default;\r\n$thumbnail-box-shadow:        0 1px 2px rgba($black,.075) !default;\r\n$thumbnail-transition:        all .2s ease-in-out !default;\r\n\r\n\r\n// Figures\r\n\r\n$figure-caption-font-size: 90% !default;\r\n$figure-caption-color:     $gray-light !default;\r\n\r\n\r\n// Breadcrumbs\r\n\r\n$breadcrumb-padding-y:          .75rem !default;\r\n$breadcrumb-padding-x:          1rem !default;\r\n$breadcrumb-item-padding:       .5rem !default;\r\n\r\n$breadcrumb-bg:                 $gray-lighter !default;\r\n$breadcrumb-divider-color:      $gray-light !default;\r\n$breadcrumb-active-color:       $gray-light !default;\r\n$breadcrumb-divider:            \"/\" !default;\r\n\r\n\r\n// Carousel\r\n\r\n$carousel-control-color:                      $white !default;\r\n$carousel-control-width:                      15% !default;\r\n$carousel-control-opacity:                    .5 !default;\r\n\r\n$carousel-indicator-width:                    30px !default;\r\n$carousel-indicator-height:                   3px !default;\r\n$carousel-indicator-spacer:                   3px !default;\r\n$carousel-indicator-active-bg:                $white !default;\r\n\r\n$carousel-caption-width:                      70% !default;\r\n$carousel-caption-color:                      $white !default;\r\n\r\n$carousel-control-icon-width:                 20px !default;\r\n\r\n$carousel-control-prev-icon-bg: str-replace(url(\"data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='#{$carousel-control-color}' viewBox='0 0 8 8'%3E%3Cpath d='M4 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E\"), \"#\", \"%23\") !default;\r\n$carousel-control-next-icon-bg: str-replace(url(\"data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='#{$carousel-control-color}' viewBox='0 0 8 8'%3E%3Cpath d='M1.5 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E\"), \"#\", \"%23\") !default;\r\n\r\n$carousel-transition:           transform .6s ease-in-out !default;\r\n\r\n\r\n// Close\r\n\r\n$close-font-size:             $font-size-base * 1.5 !default;\r\n$close-font-weight:           $font-weight-bold !default;\r\n$close-color:                 $black !default;\r\n$close-text-shadow:           0 1px 0 $white !default;\r\n\r\n\r\n// Code\r\n\r\n$code-font-size:              90% !default;\r\n$code-padding-x:              .4rem !default;\r\n$code-padding-y:              .2rem !default;\r\n$code-color:                  #bd4147 !default;\r\n$code-bg:                     $gray-lightest !default;\r\n\r\n$kbd-color:                   $white !default;\r\n$kbd-bg:                      $gray-dark !default;\r\n\r\n$pre-bg:                      $gray-lightest !default;\r\n$pre-color:                   $gray-dark !default;\r\n$pre-border-color:            #ccc !default;\r\n$pre-scrollable-max-height:   340px !default;\r\n","\r\n// map Bootstrap variables here so that Bootstrap is not required for essential layout\r\n// prefix with `l_`\r\n// Note: '_' and '-' are identical, e.g. if bootstrap _var.scss is loaded, $body_bg will be compiled with value of $body-bg\r\n\r\n// color\r\n$l_body_bg:     $body-bg;\r\n$l_white:       #fff;       // for app-page-container, quickview, header\r\n$l_gray_dark:   $gray-dark; // for nav\r\n\r\n// screen\r\n$l_screen_md_min: map-get($grid-breakpoints, lg);\r\n$l_screen_sm_max: (map-get($grid-breakpoints, lg) - 1);\r\n\r\n// zindex\r\n$l_zindex_page_container_mobile: 100;\r\n$l_zindex_sidebar_mobile:        ($l_zindex_page_container_mobile - 1);\r\n$l_zindex_navbar:                $zindex-navbar;\r\n$l_zindex_sidebar:               ($l_zindex_navbar + 1);\r\n$l_zindex_quickview:             ($l_zindex_navbar + 1);\r\n$l_zindex_overlay:               $zindex-modal-backdrop;\r\n\r\n// Layout\r\n$l_header_height:               60px;\r\n$l_mobile_header_height:        48px;\r\n\r\n$l_sidebar_width_collapsed:     64px;  // Desktop only: collpased sidebar\r\n$l_sidebar_width:               250px; // Desktop: sidebar; Mobile: off-canvas sidebar\r\n\r\n$l_right_quickview_width:       300px;\r\n\r\n$l_layout_boxed_max_width:      1200px;\r\n\r\n// Motion\r\n// https://material.google.com/motion/duration-easing.html#duration-easing-natural-easing-curves\r\n$l_transition_bezier: .05, .74, .27, .99; // for nav, quickview\r\n\r\n$l_transition_ease:          cubic-bezier(0.4,0.0,0.2,1);\r\n$l_transition_ease_out:      cubic-bezier(0.0,0.0,0.2,1); // Used on sidebar related\r\n$l_transition_ease_in:       cubic-bezier(0.4,0.0,1,1);\r\n\r\n// $l_transition_ease_out_sidebar:   cubic-bezier(.25,.75,.25,.99);\r\n$l_sidebar_collpase_duration:    .3s;\r\n$l_transition_ease_out_quickview: cubic-bezier(.05,.74,.2,.99);\r\n\r\n\r\n\r\n\r\n// Themes\r\n// --------------------------------------------------\r\n\r\n$header_line_height: 24px;\r\n$header_box_shadow:  0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12); // .z-depth-1\r\n\r\n$sidebar_footer_height:     44px;\r\n\r\n// = Default dark background sidebar =\r\n$sidebar_bg_color:                    $dark;\r\n$sidebar_text_color:                  #a1a1a1;\r\n$nav_link_hover_bg:                   transparent;\r\n$nav_link_hover_color:                #fafafa;\r\n\r\n// the reason .active bg is different from .open bg is becasue there's no border between li\r\n// if one li is .active, and sibiling is .open, then they have same bg, which is confusing\r\n$nav_link_active_bg:                  transparent;\r\n$nav_link_open_bg:                    rgba(0,0,0,.1);\r\n$nav_link_active_color:               $nav_link_hover_color;\r\n\r\n$nav_sub_bg:                          $nav_link_open_bg;\r\n$nav_sub_link_active_color:           $nav_link_hover_color;\r\n$nav_sub2_bg:                         rgba(0,0,0,.1);\r\n$nav_sub3_bg:                         rgba(0,0,0,.1);\r\n\r\n// sidebar width, just for demo\r\n// in real project, simply change '$l_sidebar_width' to any value u want\r\n$l_sidebar_width_sm:               220px;\r\n$l_sidebar_width_lg:               280px;\r\n\r\n\r\n\r\n","// layout\r\n.app-sidebar {\r\n    display: block;\r\n    z-index: $l_zindex_sidebar_mobile;\r\n    position: fixed;\r\n    left: 0;\r\n    bottom: 0;\r\n    top: 0;\r\n    height: 100vh; // Fix(Safari): Fix sidebar fail to on safari\r\n    width: $l_sidebar_width;\r\n    overflow: hidden;\r\n    //background-color: $l_gray_dark;\r\n\r\n    .sidebar-header{\r\n        display: block;\r\n        position: relative;\r\n        height: $l_header_height;\r\n        .logo{\r\n            width: 87%;\r\n            height: 87%;\r\n            position: absolute;\r\n            top: 5px;\r\n            left: 0px;\r\n            &.full-size{\r\n                width:100%;\r\n                opacity:1;\r\n            }\r\n            &.min-size{\r\n                width:100%;\r\n                opacity:0;\r\n            }\r\n        }\r\n    }\r\n\r\n  .sidebar-footer {\r\n    background-color: $l_gray_dark; // needed, otherwise content below will show up\r\n  }\r\n}\r\n\r\n// desktop\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n  .app-sidebar {\r\n    z-index: $l_zindex_sidebar;\r\n    overflow-x: hidden;\r\n  }\r\n}\r\n",".quickpanel{\r\n\tdisplay: none;\r\n    z-index: $l_zindex_sidebar_mobile;\r\n    position: fixed;\r\n    right: 0;\r\n    bottom: 0;\r\n    top: 0;\r\n    height: 100vh; // Fix(Safari): Fix sidebar fail to on safari\r\n    width: $l_sidebar_width;\r\n    overflow: hidden;\r\n    background-color: $l_gray_dark;\r\n}\r\n",".app-page-container {\r\n  width: 100%;\r\n  height: 100%;\r\n\r\n  .app-content-wrapper{\r\n    min-height: 100%;\r\n    position: relative;\r\n    //background-color: $l_white; // important! otherwise trasparent background-color won't cover the sidebar, when scroll down\r\n    .app-content {\r\n      z-index: 10;\r\n      padding-bottom: $sidebar_footer_height;\r\n      min-height: 100%;\r\n      transition: all 0.3s ease;\r\n      &.full-width{\r\n        width: 100%;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// for fixed-header\r\n.fixed-header {\r\n  .app-page-container {\r\n    .app-content-wrapper {\r\n      .app-content {\r\n        padding-top: $l_header_height;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// for md-sidenav-right\r\n.app-page-container {\r\n  &.scroll-disabled {\r\n    overflow: hidden;\r\n  }\r\n}\r\n\r\n// mobile\r\n@media only screen and (max-width: $l_screen_sm_max) {\r\n  .app-page-container {\r\n    position: relative;\r\n    z-index: $l_zindex_page_container_mobile;\r\n    padding-left: 0;\r\n    transition: transform .25s ease;\r\n    background-color: $l_white; // important! otherwise trasparent background-color won't cover the sidebar\r\n    .app-content-wrapper {\r\n      .app-content  {\r\n        overflow-x: hidden;\r\n        // height: auto; // minimizes address bar on scroll\r\n      }\r\n    }\r\n  }\r\n\r\n  // off-canvas sidebar\r\n  .sidebar-mobile-open {\r\n    .app-page-container{\r\n      overflow: hidden;\r\n      position: fixed;\r\n      transform: translateX($l_sidebar_width);\r\n    }\r\n  }\r\n}\r\n// desktop\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n  .app-page-container {\r\n    .app-content-wrapper {\r\n      .app-content {\r\n        padding-left: $l_sidebar_width;\r\n      }\r\n      .app-footer{\r\n        left: $l_sidebar_width;\r\n      }\r\n    }\r\n  }\r\n\r\n  .nav-collapsed {\r\n    .app-page-container {\r\n      .app-content-wrapper {\r\n        .app-content {\r\n          padding-left: $l_sidebar_width_collapsed;\r\n        }\r\n        .app-footer {\r\n          left: $l_sidebar_width_collapsed;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n",".app-page-container {\r\n  .app-footer{\r\n    position: absolute;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    transition: left $l_sidebar_collpase_duration $l_transition_ease_out;\r\n    &.fixed{\r\n      position: fixed;\r\n    }\r\n  }\r\n}\r\n\r\n// theme\r\n.app-page-container {\r\n  .app-footer{\r\n    // make footer same height with sidebar footer\r\n    padding: 13px 0;\r\n    border-top: 1px solid rgba(0,0,0,.05);\r\n    font-size: 11px;\r\n    line-height: 17px;\r\n    color: $text-muted;\r\n\r\n    .brand {\r\n      color: $body-color;\r\n      text-transform: uppercase;\r\n      letter-spacing: 0.02em;\r\n    }\r\n    \r\n    .material-icons {\r\n      font-size: $font-size-sm;\r\n      vertical-align: text-top;\r\n    }   \r\n  }\r\n}\r\n","// layout\r\n// ----------\r\n.quickview-wrapper{\r\n  z-index: $l_zindex_quickview;\r\n  position: fixed;\r\n  top: 0;\r\n  bottom: 0;\r\n  height: 100vh; // Fix(Safari): Fix sidebar fail to on safari\r\n  right: -$l_right_quickview_width;\r\n  width: $l_right_quickview_width;\r\n  background: $l_white;\r\n  transition: right .4s $l_transition_ease_out_quickview; \r\n  backface-visibility: hidden;\r\n}\r\n\r\n.quickview-open {\r\n  .quickview-wrapper {\r\n    right: 0;\r\n  }\r\n}\r\n\r\n\r\n// theme\r\n// ----------\r\n.quickview-wrapper {\r\n  border-left: 1px solid rgba(0,0,0,.05);\r\n  box-shadow: 0 0 8px rgba(0,0,0,.1);\r\n}\r\n","\r\n// layout\r\n.app-overlay{\r\n  // display: none;\r\n  opacity: 0;\r\n  visibility: hidden;\r\n  transition: opacity .3s ease, \r\n  visibility .3s ease; // vibisility also needs to be transitional, otherwise there won't be an exit transition\r\n\r\n  z-index: $l_zindex_overlay;\r\n  position: fixed;\r\n  left: 0;\r\n  right: 0;\r\n  top: 0;\r\n  bottom: 0;\r\n  background: $l_white;\r\n  overflow: hidden;\r\n\r\n  .overlay-close{\r\n    position: absolute;\r\n    right: 20px;\r\n    top: 25px;\r\n  }\r\n}\r\n.overlay-active {\r\n  .app-overlay {\r\n    // display: block;\r\n    visibility: visible;\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n","// Desktop only, for boxed layout\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n  .layout-boxed {\r\n    &.full-height {\r\n      max-width: $l_layout_boxed_max_width;\r\n      margin: auto;\r\n      box-shadow: 0 0 1px rgba(0,0,0,.2);\r\n\r\n      .app-sidebar {\r\n        left: auto;\r\n      }\r\n\r\n\r\n      .app-header {\r\n        // in boxed mode, .app-header position is the same, only style of .app-header-inner is changed\r\n        > .app-header-inner {\r\n          max-width: $l_layout_boxed_max_width;\r\n          margin: auto;\r\n          box-shadow: 0 1px 1px rgba(0,0,0,.2); // Note: it overrides default $header_box_shadow\r\n        }\r\n      }\r\n\r\n      .app-overlay {\r\n        > .app-overlay-inner {\r\n          max-width: $l_layout_boxed_max_width;\r\n          margin: auto;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}","// Mobile\r\n@media only screen and (max-width: $l_screen_sm_max) {\r\n  .no-app-sidebar {\r\n    & + .app-page-container {\r\n      .app-header {\r\n        .header-icon {\r\n          display: none;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// Desktop only, hide app-sidebar\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n  .no-app-sidebar {\r\n    .app-sidebar {\r\n      // removed ng-include, so there won't be an app-sidebar\r\n    }\r\n\r\n    & + .app-page-container {\r\n      .app-header {\r\n        .brand {\r\n          padding-left: 0;\r\n        }\r\n      }\r\n\r\n      .app-content-wrapper {\r\n        .app-content {\r\n          padding-left: 0;\r\n        } \r\n        .app-footer{\r\n          left: 0;\r\n        }\r\n      }\r\n\r\n    }\r\n  }\r\n\r\n}","// for Directive: accordionNav\r\n.app-sidebar ul.nav {\r\n  flex-direction: column;\r\n  ul {\r\n    display: none; // hide all sub ul on init\r\n  }\r\n\r\n  li {\r\n    position: relative;\r\n\r\n    &.open {\r\n      > .icon-has-ul { // child only\r\n        transform: rotate(-180deg);\r\n      }\r\n    }\r\n  }\r\n\r\n  // icon for list has sub ul\r\n  .icon-has-ul {\r\n    position: absolute;\r\n    top: 12px;\r\n    right: 15px;\r\n    font-size: 18px;\r\n    line-height: 1;\r\n    color: #777;\r\n    transition: transform .3s ease-in-out;\r\n  }\r\n\r\n  ul {\r\n    .icon-has-ul {\r\n      top: 11px;\r\n    }\r\n  }\r\n\r\n  .nav-divider {\r\n    background-color: rgba(#000, .15);\r\n    min-height: 1px;\r\n    margin: 10px 0 10px;\r\n    overflow: hidden;\r\n  }\r\n}\r\n\r\n// for Directive: slimScroll\r\n.app-sidebar{\r\n  .sidebar-content {\r\n    height: calc(100% - #{$l_header_height + $sidebar_footer_height} ); // calc is a simple sultion with a bit cross browser support problem\r\n  }\r\n}","// toggler\r\n// ------\r\n.sidebar-header {\r\n    a.collapsednav-toggler {\r\n        display: inline-block;\r\n        position: absolute;\r\n        width: 20px;\r\n        height: 20px;\r\n        line-height: 20px;\r\n        text-align: center;\r\n        right: 18px;\r\n        top: 20px; // (60 - 20) /2\r\n        color: rgba(255,255,255,.55);\r\n        // padding: 0 5px;\r\n        .material-icons {\r\n            font-size: 12px;\r\n        }\r\n    }\r\n\r\n}\r\n.sidebar-sm.nav-collapsed .app-sidebar:hover {\r\n    width: 220px;\r\n}\r\n.sidebar-sm, .sidebar-lg{\r\n    &.nav-collapsed{\r\n        .app-sidebar{\r\n            .sidebar-header {\r\n\r\n                .logo{\r\n                    width: 87%;\r\n                    height: 87%;\r\n                    position: absolute;\r\n                    top: 5px;\r\n                    left: 0px;\r\n                    &.full-size{\r\n                        width:100%;\r\n                        opacity:0;\r\n                    }\r\n                    &.min-size{\r\n                        width:100%;\r\n                        opacity:1;\r\n                    }\r\n                }\r\n            }\r\n            &:hover{\r\n                .sidebar-header {\r\n\r\n                    .logo{\r\n                        &.full-size{\r\n                            width:100%;\r\n                            opacity:1;\r\n                        }\r\n                        &.min-size{\r\n                            width:100%;\r\n                            opacity:0;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n// mobile\r\n@media only screen and (max-width: $l_screen_sm_max ) {\r\n    .app-sidebar{\r\n        .sidebar-header {\r\n            a.collapsednav-toggler {\r\n                display: none;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n\r\n// When hover over collapsed nav, it becomes normal nav again, the css is used twice, both normal state & hover state\r\n// So put those shared style in mixins, call those mixins once when it's normal nav, and another when hover over collapsed nav\r\n\r\n@mixin normalSidebarHeader() {\r\n  text-align: left;\r\n  .logo-icon {\r\n    margin-right: 11px;\r\n  }\r\n  .logo-img {\r\n    margin-right: 16px;\r\n  }\r\n  .brand {\r\n    display: inline;\r\n  }\r\n  .collapsednav-toggler {\r\n    display: inline-block;\r\n  }\r\n}\r\n@mixin normalSidebarContent() {\r\n  .nav-header {\r\n    display: block;\r\n  }\r\n  .nav-text {\r\n    display: inline;\r\n  }\r\n  .icon-has-ul {\r\n    display: inherit;\r\n  }\r\n  .badge {\r\n    display: inherit;\r\n    top: 14px;\r\n    right: 35px;\r\n  }\r\n\r\n  .nav > li {\r\n    // 1st level ul\r\n    > a {\r\n      padding: 10px 16px !important;\r\n      text-align: left !important;\r\n      .nav-icon {\r\n        transition: padding .35s $l_transition_ease_out,\r\n        margin  .35s $l_transition_ease_out;\r\n        margin-right: 18px;\r\n      }\r\n      // > .badge {\r\n      //   top: 12px;\r\n      // }\r\n    }\r\n\r\n    // >= 2nd level ul\r\n    ul {\r\n      li {\r\n        > a {\r\n          text-align: left !important;\r\n          > span {\r\n            display: inline;\r\n          }\r\n\r\n          > .material-icons {\r\n            transition: margin .35s $l_transition_ease_out;\r\n            margin-right: 18px;\r\n          }\r\n          > div {\r\n            > span { // for materail-ui FlatButton\r\n              display: inline;\r\n            }\r\n            > .material-icons {\r\n              transition: margin .35s $l_transition_ease_out;\r\n              margin-right: 18px;\r\n            }\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n@mixin normalSidebarFooter() {\r\n  .nav-text {\r\n    display: inline;\r\n  }\r\n  .nav > li {\r\n    > a {\r\n      padding: 10px 16px !important;\r\n      text-align: left !important;\r\n      .nav-icon {\r\n        margin-right: 15px;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// normal\r\n.app-sidebar {\r\n  .sidebar-header {\r\n    @include normalSidebarHeader();\r\n  }\r\n  .sidebar-content {\r\n    @include normalSidebarContent();\r\n  }\r\n  .sidebar-footer {\r\n    @include normalSidebarFooter();\r\n  }\r\n}\r\n\r\n\r\n// desktop\r\n// nav-collapsed, .nav-behind is desktop only\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n\r\n  // default style is normal nav\r\n  .app-sidebar {\r\n    transition: width $l_sidebar_collpase_duration $l_transition_ease_out;\r\n    white-space: nowrap;\r\n  }\r\n\r\n  // when collapsed\r\n  .nav-collapsed .app-sidebar{\r\n    width: $l_sidebar_width_collapsed;\r\n\r\n    .sidebar-header {\r\n      text-align: center;\r\n      .logo-icon,\r\n      .logo-img {\r\n        margin-right: 0;\r\n      }\r\n      .brand {\r\n        display: none;\r\n      }\r\n      .collapsednav-toggler {\r\n        display: none;\r\n      }\r\n    }\r\n\r\n    .sidebar-content {\r\n      .nav-header,\r\n      .nav-text,\r\n      .icon-has-ul,\r\n      // .badge,\r\n      .nav ul a > span { // sub ul text\r\n        display: none;\r\n      }\r\n      .nav ul a > div { // for materail-ui FlatButton\r\n        > span {\r\n          display: none;\r\n        }\r\n      }\r\n\r\n      .badge {\r\n        top: 3px;\r\n        right: 5px;\r\n      }\r\n\r\n      .nav > li {\r\n        > a {\r\n          padding: 12px 16px !important;\r\n          text-align: center !important;\r\n        }\r\n\r\n        .nav-icon {\r\n          margin-right: 0;\r\n        }\r\n\r\n        // because of jQuery\r\n        ul {\r\n          li {\r\n            > a {\r\n              text-align: center !important;\r\n              > .material-icons {\r\n                margin-right: 0;\r\n              }\r\n\r\n              > div > .material-icons { // for materail-ui FlatButton\r\n                margin-right: 0;\r\n              }\r\n            }\r\n          }\r\n        }\r\n      }\r\n    }\r\n\r\n    .sidebar-footer {\r\n      .nav-text {\r\n        display: none;\r\n      }\r\n      .nav > li {\r\n        > a {\r\n          text-align: center !important;\r\n        }\r\n\r\n        .nav-icon {\r\n          margin-right: 0;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  // reset to normal style\r\n  .nav-collapsed .app-sidebar:hover{\r\n    width: $l_sidebar_width;\r\n\r\n    // solve \".badge, .icon-has-ul floating on text when hover over collapsed nav\" problem\r\n    // however this effect doesn't apply to remove/add `.nav-collapsed` class using ng-class\r\n    > * {\r\n      width: $l_sidebar_width;\r\n    }\r\n\r\n    .sidebar-header {\r\n      @include normalSidebarHeader();\r\n    }\r\n    .sidebar-content {\r\n      @include normalSidebarContent();\r\n    }\r\n    .sidebar-footer {\r\n      @include normalSidebarFooter();\r\n    }\r\n  }\r\n}\r\n","// .nav-behind is desktop only\r\n// when active, sidebar will show behind top header\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n  .nav-behind {\r\n    .app-sidebar {\r\n      z-index: $l_zindex_navbar - 1;\r\n    }\r\n  }\r\n}","// Shared\r\n// --------------------------------------------------\r\n.sidebar-sm,\r\n.sidebar-lg {\r\n\r\n\r\n  // page-container\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    &.nav-collapsed {\r\n      .app-page-container {\r\n        .app-content-wrapper {\r\n          .app-content {\r\n            padding-left: $l_sidebar_width_collapsed;\r\n          }\r\n          .app-footer {\r\n            left: $l_sidebar_width_collapsed;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  // sidebar-collapsed\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    &.nav-collapsed .app-sidebar{\r\n      width: $l_sidebar_width_collapsed;\r\n    }\r\n  }\r\n\r\n\r\n}\r\n\r\n\r\n\r\n// Small\r\n// --------------------------------------------------\r\n.sidebar-sm {\r\n\r\n\r\n  // header\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    .app-header {\r\n      .brand {\r\n        width: $l_sidebar_width_sm;\r\n      }\r\n    }\r\n  }\r\n\r\n  // page-container\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    .app-page-container {\r\n      .app-content-wrapper {\r\n        .app-content {\r\n          padding-left: $l_sidebar_width_sm;\r\n        } \r\n        .app-footer{\r\n          left: $l_sidebar_width_sm;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  @media only screen and (max-width: $l_screen_sm_max) {\r\n    // off-canvas sidebar\r\n    &.sidebar-mobile-open {\r\n      .app-page-container {\r\n        transform: translateX($l_sidebar_width_sm);\r\n      }\r\n    }\r\n  }\r\n\r\n  // sidebar\r\n  .app-sidebar {\r\n    width: $l_sidebar_width_sm;\r\n  }\r\n\r\n  // sidebar-collapsed\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    &.nav-collapsed .app-sidebar:hover{\r\n      width: $l_sidebar_width_sm;\r\n      > * {\r\n        width: $l_sidebar_width_sm;\r\n      }\r\n    }\r\n  }\r\n\r\n\r\n}\r\n\r\n\r\n\r\n\r\n\r\n// Large\r\n// --------------------------------------------------\r\n.sidebar-lg {\r\n\r\n\r\n  // header\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    .app-header {\r\n      .brand {\r\n        width: $l_sidebar_width_lg;\r\n      }\r\n    }\r\n  }\r\n\r\n  // page-container\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    .app-page-container {\r\n      .app-content-wrapper {\r\n        .app-content {\r\n          padding-left: $l_sidebar_width_lg;\r\n        } \r\n        .app-footer{\r\n          left: $l_sidebar_width_lg;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  @media only screen and (max-width: $l_screen_sm_max) {\r\n    // off-canvas sidebar\r\n    &.sidebar-mobile-open {\r\n      .app-page-container {\r\n        transform: translateX($l_sidebar_width_lg);\r\n      }\r\n    }\r\n  }\r\n\r\n  // sidebar\r\n  .app-sidebar {\r\n    width: $l_sidebar_width_lg;\r\n  }\r\n\r\n  // sidebar-collapsed\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    &.nav-collapsed .app-sidebar:hover{\r\n      width: $l_sidebar_width_lg;\r\n      > * {\r\n        width: $l_sidebar_width_lg;\r\n      }\r\n    }\r\n  }\r\n\r\n\r\n}","// theme\r\n.app-header {\r\n  padding: 0;\r\n  border: 0; // no border, otherwise other elements' height can only be ($l_header_height -1)\r\n  text-align: center;\r\n\r\n  .app-header-inner {\r\n    height: $l_header_height;\r\n    box-shadow: $header_box_shadow;\r\n  }\r\n  @media only screen and (min-width: $l_screen_md_min) {\r\n    text-align: inherit;\r\n  }\r\n\r\n  &.bg-transparent {\r\n    background-color: transparent !important;\r\n  }\r\n\r\n  .brand {\r\n    display: inline-block;\r\n    text-align: center;\r\n    float: left; // becasue of alignment problem with md-button in .top-nav-left\r\n\r\n    h2 {\r\n      font-size: 30px;\r\n      margin: 0;\r\n      line-height: $l_header_height;\r\n    }\r\n  }\r\n\r\n  .header-icon {\r\n    display: inline-block;\r\n    height: $l_header_height;\r\n    padding: ( ($l_header_height -  $header_line_height)/2 ) 14px;\r\n    @media only screen and (min-width: $l_screen_md_min) {\r\n      padding-left: 18px;\r\n      padding-right: 18px;\r\n    }\r\n    .material-icons {\r\n      font-size: 24px;\r\n    }\r\n  }\r\n}\r\n\r\n.top-nav-left,\r\n.top-nav-right {\r\n  > ul > .list-inline-item {\r\n    margin: 0;\r\n    padding: 0;\r\n  }\r\n}\r\n\r\n.top-nav-left {\r\n  display: inline-block;\r\n  > ul {\r\n    display: inline;\r\n    margin: 0;\r\n  }\r\n}\r\n\r\n.top-nav-right {\r\n  display: inline-block;\r\n  font-size: 16px;\r\n  line-height: $header_line_height;\r\n  float: right;\r\n\r\n  ul {\r\n    margin: 0;\r\n  }\r\n\r\n\r\n  // \r\n  li {\r\n    height: $l_header_height;\r\n    float: left;\r\n  }\r\n\r\n  a {\r\n    &:hover,\r\n    &:focus {\r\n      text-decoration: none;\r\n    }\r\n  }\r\n}\r\n",".logo-img {\r\n  width: 24px;\r\n  height: 24px;\r\n  margin-bottom: -3px;\r\n  margin-right: 12px;\r\n  // .st0 { fill: #fff; }\r\