are\r\n * distributed under the License is distributed on an \"AS IS\" BASIS,\r\n * WITHOUT WARRANTIES OR CONDITIONS OF ANY KIND, either express or implied.\r\n * See the License for the specific language governing permissions and\r\n * limitations under the License.\r\n */\r\n\r\n/* Typography */\r\n\r\n@mixin typo-preferred-font($usePreferred: true) {\r\n  @if $usePreferred {\r\n  font-family: $preferred_font;\r\n  }\r\n}\r\n\r\n@mixin typo-display-4($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 112px;\r\n  font-weight: 300;\r\n  line-height: 1;\r\n  letter-spacing: -0.04em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-display-3($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 56px;\r\n  font-weight: 400;\r\n  line-height: 1.35;\r\n  letter-spacing: -0.02em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-display-2($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 45px;\r\n  font-weight: 400;\r\n  line-height: 48px;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-display-1($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 34px;\r\n  font-weight: 400;\r\n  line-height: 40px;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-headline($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 24px;\r\n  font-weight: 400;\r\n  line-height: 32px;\r\n  -moz-osx-font-smoothing: grayscale;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-title($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 20px;\r\n  font-weight: 500;\r\n  line-height: 1;\r\n  letter-spacing: 0.02em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-subhead($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 16px;\r\n  font-weight: 400;\r\n  line-height: 24px;\r\n  letter-spacing: 0.04em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-subhead-2($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 16px;\r\n  font-weight: 400;\r\n  line-height: 28px;\r\n  letter-spacing: 0.04em;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-body-2($colorContrast: false, $usePreferred: false) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  @if $usePreferred {\r\n  font-weight: 500;\r\n  } @else {\r\n  font-weight: bold;\r\n  }\r\n  line-height: 24px;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-body-1($colorContrast: false, $usePreferred: false) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  font-weight: 400;\r\n  line-height: 24px;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-caption($colorContrast: false, $usePreferred: false) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 12px;\r\n  font-weight: 400;\r\n  line-height: 1;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-blockquote($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  position: relative;\r\n  font-size: 24px;\r\n  font-weight: 300;\r\n  font-style: italic;\r\n  line-height: 1.35;\r\n  letter-spacing: 0.08em;\r\n\r\n  &:before {\r\n  position: absolute;\r\n  left: -0.5em;\r\n  content: '';\r\n  }\r\n\r\n  &:after {\r\n  content: '';\r\n  margin-left: -0.05em;\r\n  }\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.54;\r\n  }\r\n}\r\n\r\n@mixin typo-menu($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  line-height: 1;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-button($colorContrast: false, $usePreferred: true) {\r\n  @include typo-preferred-font($usePreferred);\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  text-transform: uppercase;\r\n  line-height: 1;\r\n  letter-spacing: 0;\r\n\r\n  @if $colorContrast {\r\n  opacity: 0.87;\r\n  }\r\n}\r\n\r\n@mixin typo-icon() {\r\n  font-family: 'Material Icons';\r\n  font-weight: normal;\r\n  font-style: normal;\r\n  font-size: 24px;\r\n  line-height: 1;\r\n  letter-spacing: normal;\r\n  text-transform: none;\r\n  display: inline-block;\r\n  word-wrap: normal;\r\n  font-feature-settings: 'liga';\r\n  -webkit-font-feature-settings: 'liga';\r\n  -webkit-font-smoothing: antialiased;\r\n}\r\n\r\n/* Shadows */\r\n\r\n// Focus shadow mixin.\r\n@mixin focus-shadow() {\r\n  box-shadow: 0 0 8px rgba(0,0,0,.18),0 8px 16px rgba(0,0,0,.36);\r\n}\r\n\r\n@mixin shadow-2dp() {\r\n  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 3px 1px -2px rgba(0, 0, 0, $shadow-key-umbra-opacity),\r\n        0 1px 5px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);\r\n}\r\n@mixin shadow-3dp() {\r\n  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 3px 3px -2px rgba(0, 0, 0, $shadow-key-umbra-opacity),\r\n        0 1px 8px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);\r\n}\r\n@mixin shadow-4dp() {\r\n  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 1px 10px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 2px 4px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n@mixin shadow-6dp() {\r\n  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 1px 18px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 3px 5px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n@mixin shadow-8dp() {\r\n  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 3px 14px 2px rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 5px 5px -3px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n\r\n@mixin shadow-16dp() {\r\n  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0  6px 30px 5px rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0  8px 10px -5px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n\r\n@mixin shadow-24dp() {\r\n  box-shadow: 0  9px 46px  8px rgba(0, 0, 0, $shadow-key-penumbra-opacity),\r\n        0 11px 15px -7px rgba(0, 0, 0, $shadow-ambient-shadow-opacity),\r\n        0 24px 38px  3px rgba(0, 0, 0, $shadow-key-umbra-opacity);\r\n}\r\n\r\n/* Animations */\r\n\r\n@mixin material-animation-fast-out-slow-in($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-fast-out-slow-in;\r\n}\r\n\r\n@mixin material-animation-linear-out-slow-in($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-linear-out-slow-in;\r\n}\r\n\r\n@mixin material-animation-fast-out-linear-in($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-fast-out-linear-in;\r\n}\r\n\r\n@mixin material-animation-default($duration:0.2s) {\r\n  transition-duration: $duration;\r\n  transition-timing-function: $animation-curve-default;\r\n}\r\n\r\n/* Dialog */\r\n\r\n@mixin dialog-width($units:5) {\r\n  @if(type_of($units) != 'number') {\r\n  @error \"The unit given to dialog-width should be a number.\";\r\n  }\r\n  // 56dp is the base unit width for Dialogs.\r\n  // With 5 units being the number of units for a mobile device.\r\n  // https://goo.gl/sK2O5o\r\n  width: $units * 56px;\r\n}\r\n","\r\n// // UI\r\n\r\ndiv.btn-w-xs { min-width: 80px !important; }\r\ndiv.btn-w-sm { min-width: 100px !important; }\r\ndiv.btn-w-md { min-width: 135px !important; }\r\ndiv.btn-w-lg { min-width: 160px !important; }\r\n\r\n","\r\n// Sidebar\r\n.app-sidebar {\r\n  .nav-icon,\r\n  .nav-text {\r\n    vertical-align: middle;\r\n  }\r\n  .nav-icon {\r\n    margin-top: -2px; // to make icon and text vertically aligned\r\n  }\r\n\r\n  .nav {\r\n    li {\r\n      > a {\r\n        display: block !important;\r\n        text-align: left !important;\r\n        min-width: inherit !important;\r\n        padding: 10px 16px;\r\n        border-radius: 0 !important;\r\n        height: inherit !important;\r\n        line-height: 24px !important;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n// \r\n.app-header {\r\n  .bg-color-dark,\r\n  .bg-color-primary,\r\n  .bg-color-success,\r\n  .bg-color-info,\r\n  .bg-color-danger {\r\n    .header-btn {\r\n      color: #fff !important;\r\n    }\r\n  }\r\n}\r\n\r\n.header-icon-dropdown-item {\r\n  font-size: 14px !important;\r\n\r\n  > div > div {\r\n    padding-left: 56px !important;\r\n  }\r\n\r\n  .text-muted {\r\n    font-size: $font-size-xs !important;\r\n  }\r\n}\r\n","\r\n.theme-dark,\r\n.theme-gray {\r\n  .app-header {\r\n    .bg-color-light,\r\n    .bg-color-warning {\r\n      .header-btn {\r\n        color: #fff !important;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.quickview-wrapper {\r\n  .customizer-layout-options {\r\n    label {\r\n      color: $body-color !important;\r\n    }\r\n\r\n    .sidebar-width-select > div {\r\n      // select text\r\n      > div > div {\r\n        color: $body-color !important;\r\n      }\r\n\r\n      > hr {\r\n        border-color: rgba(#000, .15) !important;\r\n      }\r\n    }\r\n  }\r\n}","\r\n// Layout fix\r\n#app-container,\r\n#app-inner,\r\n.main-app-container {\r\n  height: 100%;\r\n}\r\n\r\n// React logo, #67DAF9\r\n.logo-react {\r\n  .react-dot,\r\n  .react-curve {\r\n    opacity: .9;\r\n  }\r\n  .react-dot {\r\n    fill: #fff;\r\n  }\r\n  .react-curve {\r\n    stroke: #fff;\r\n  }\r\n}\r\n.bg-color-warning,\r\n.bg-color-light {\r\n  > .logo-react {\r\n    .react-dot {\r\n      fill: $body-color;\r\n    }\r\n    .react-curve {\r\n      stroke: $body-color;\r\n    }\r\n  }\r\n}\r\n.theme-gray,\r\n.theme-dark {\r\n  .bg-color-warning,\r\n  .bg-color-light {\r\n    > .logo-react {\r\n      .react-dot,\r\n      .react-curve {\r\n        opacity: .7;\r\n      }\r\n      .react-dot {\r\n        fill: #fff;\r\n      }\r\n      .react-curve {\r\n        stroke: #fff;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n@media only screen and (min-width: $l_screen_md_min) {\r\n  .nav-collapsed .app-sidebar{\r\n    .sidebar-header {\r\n      .logo-react {\r\n        margin-bottom: -5px;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n","// Variables\r\n//\r\n// Copy settings from this file into the provided `_custom.scss` to override\r\n// the Bootstrap defaults without modifying key, versioned files.\r\n\r\n\r\n// Table of Contents\r\n//\r\n// Colors\r\n// Options\r\n// Spacing\r\n// Body\r\n// Links\r\n// Grid breakpoints\r\n// Grid containers\r\n// Grid columns\r\n// Fonts\r\n// Components\r\n// Tables\r\n// Buttons\r\n// Forms\r\n// Dropdowns\r\n// Z-index master list\r\n// Navbar\r\n// Navs\r\n// Pagination\r\n// Jumbotron\r\n// Form states and alerts\r\n// Cards\r\n// Tooltips\r\n// Popovers\r\n// Badges\r\n// Modals\r\n// Alerts\r\n// Progress bars\r\n// List group\r\n// Image thumbnails\r\n// Figures\r\n// Breadcrumbs\r\n// Carousel\r\n// Close\r\n// Code\r\n\r\n@mixin _assert-ascending($map, $map-name) {\r\n  $prev-key: null;\r\n  $prev-num: null;\r\n  @each $key, $num in $map {\r\n    @if $prev-num == null {\r\n      // Do nothing\r\n    } @else if not comparable($prev-num, $num) {\r\n      @warn \"Potentially invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} whose unit makes it incomparable to #{$prev-num}, the value of the previous key '#{$prev-key}' !\";\r\n    } @else if $prev-num >= $num {\r\n      @warn \"Invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} which isn't greater than #{$prev-num}, the value of the previous key '#{$prev-key}' !\";\r\n    }\r\n    $prev-key: $key;\r\n    $prev-num: $num;\r\n  }\r\n}\r\n\r\n// Replace `$search` with `$replace` in `$string`\r\n// @author Hugo Giraudel\r\n// @param {String} $string - Initial string\r\n// @param {String} $search - Substring to replace\r\n// @param {String} $replace ('') - New value\r\n// @return {String} - Updated string\r\n@function str-replace($string, $search, $replace: \"\") {\r\n  $index: str-index($string, $search);\r\n\r\n  @if $index {\r\n    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);\r\n  }\r\n\r\n  @return $string;\r\n}\r\n\r\n@mixin _assert-starts-at-zero($map) {\r\n  $values: map-values($map);\r\n  $first-value: nth($values, 1);\r\n  @if $first-value != 0 {\r\n    @warn \"First breakpoint in `$grid-breakpoints` must start at 0, but starts at #{$first-value}.\";\r\n  }\r\n}\r\n\r\n\r\n// General variable structure\r\n//\r\n// Variable format should follow the `$component-modifier-state-property` order.\r\n\r\n\r\n// Colors\r\n//\r\n// Grayscale and brand colors for use across Bootstrap.\r\n\r\n// Start with assigning color names to specific hex values.\r\n$white:  #fff !default;\r\n$black:  #000 !default;\r\n$red:    #EF5350 !default; // Red 400\r\n$orange: #FFCA28 !default; // Amber 400\r\n$yellow: #ffd500 !default;\r\n$green:  #66BB6A !default; // Green 400\r\n$blue:   #2196F3 !default; // Blue 500\r\n$teal:   #5bc0de !default;\r\n$pink:   #ff5b77 !default;\r\n$purple: #7E57C2 !default; // Deep Purple 400\r\n\r\n// Create grayscale\r\n$gray-darker:               #222 !default;\r\n$gray-dark:                 #292b2c !default;\r\n$gray:                      #464a4c !default;\r\n$gray-light:                #636c72 !default;\r\n$gray-lighter:              #eceeef !default;\r\n$gray-lightest:             #f7f7f9 !default;\r\n\r\n//default\r\n$color0: rgba(48, 48, 48, 0);\r\n$color1: #d0d2d7;\r\n$color2: #A6956D;\r\n$color3: #21242d;\r\n$color4: #2D323E;\r\n$color5: #AA3300;\r\n$color6: lighten($color4, 10%);\r\n$color7: lighten($color4, 30%);\r\n$color8: #6d6146;\r\n\r\n\r\n// Reassign color vars to semantic color scheme\r\n$brand-primary:             $blue !default;\r\n$brand-success:             $green !default;  // Accent\r\n$brand-info:                #00BCD4 !default; // Cyan 500\r\n$brand-info-alt:            $purple !default;\r\n$brand-warning:             $orange !default;\r\n$brand-danger:              $red !default;\r\n$brand-inverse:             $gray-dark !default;\r\n\r\n\r\n$brand-default:             $color5 !default;\r\n$brand-secondary:           $color2 !default;\r\n\r\n\r\n// Options\r\n//\r\n// Quickly modify global styling by enabling or disabling optional features.\r\n\r\n$enable-rounded:            true !default;\r\n$enable-shadows:            false !default;\r\n$enable-gradients:          false !default;\r\n$enable-transitions:        true !default;\r\n$enable-hover-media-query:  false !default;\r\n$enable-grid-classes:       true !default;\r\n$enable-print-styles:       true !default;\r\n\r\n\r\n// Spacing\r\n//\r\n// Control the default styling of most Bootstrap elements by modifying these\r\n// variables. Mostly focused on spacing.\r\n// You can add more entries to the $spacers map, should you need more variation.\r\n\r\n$spacer:   1rem !default;\r\n$spacer-x: $spacer !default;\r\n$spacer-y: $spacer !default;\r\n$spacers: (\r\n  0: (\r\n    x: 0,\r\n    y: 0\r\n  ),\r\n  1: (\r\n    x: ($spacer-x * .25),\r\n    y: ($spacer-y * .25)\r\n  ),\r\n  2: (\r\n    x: ($spacer-x * .5),\r\n    y: ($spacer-y * .5)\r\n  ),\r\n  3: (\r\n    x: $spacer-x,\r\n    y: $spacer-y\r\n  ),\r\n  4: (\r\n    x: ($spacer-x * 1.5),\r\n    y: ($spacer-y * 1.5)\r\n  ),\r\n  5: (\r\n    x: ($spacer-x * 3),\r\n    y: ($spacer-y * 3)\r\n  )\r\n) !default;\r\n$border-width: 1px !default;\r\n\r\n// This variable affects the `.h-*` and `.w-*` classes.\r\n$sizes: (\r\n  25: 25%,\r\n  50: 50%,\r\n  75: 75%,\r\n  100: 100%\r\n) !default;\r\n\r\n// Body\r\n//\r\n// Settings for the `<body>` element.\r\n\r\n$body-bg:       #e5e5e5 !default;\r\n$body-color:    rgba(0, 0, 0, 0.87) !default;\r\n$inverse-bg:    $gray-dark !default;\r\n$inverse-color: $gray-lighter !default;\r\n\r\n\r\n// Links\r\n//\r\n// Style anchor elements.\r\n\r\n$link-color:            $brand-primary !default;\r\n$link-decoration:       none !default;\r\n$link-hover-color:      darken($link-color, 15%) !default;\r\n$link-hover-decoration: underline !default;\r\n\r\n\r\n// Grid breakpoints\r\n//\r\n// Define the minimum dimensions at which your layout will change,\r\n// adapting to different screen sizes, for use in media queries.\r\n\r\n$grid-breakpoints: (\r\n  xs: 0,\r\n  sm: 576px,\r\n  md: 768px,\r\n  lg: 992px,\r\n  xl: 1200px\r\n) !default;\r\n@include _assert-ascending($grid-breakpoints, \"$grid-breakpoints\");\r\n@include _assert-starts-at-zero($grid-breakpoints);\r\n\r\n\r\n// Grid containers\r\n//\r\n// Define the maximum width of `.container` for different screen sizes.\r\n\r\n$container-max-widths: (\r\n  sm: 540px,\r\n  md: 720px,\r\n  lg: 960px,\r\n  xl: 1140px\r\n) !default;\r\n@include _assert-ascending($container-max-widths, \"$container-max-widths\");\r\n\r\n\r\n// Grid columns\r\n//\r\n// Set the number of columns and specify the width of the gutters.\r\n\r\n$grid-columns:               12 !default;\r\n$grid-gutter-width-base:     30px !default;\r\n$grid-gutter-widths: (\r\n  xs: $grid-gutter-width-base,\r\n  sm: $grid-gutter-width-base,\r\n  md: $grid-gutter-width-base,\r\n  lg: $grid-gutter-width-base,\r\n  xl: $grid-gutter-width-base\r\n) !default;\r\n\r\n// Fonts\r\n//\r\n// Font, line-height, and color for body text, headings, and more.\r\n\r\n$font-family-sans-serif: \"Roboto\", -apple-system, system-ui, BlinkMacSystemFont, \"Segoe UI\", Roboto, \"Helvetica Neue\", Arial, sans-serif !default;\r\n$font-family-serif:      Georgia, \"Times New Roman\", Times, serif !default;\r\n$font-family-monospace:  Menlo, Monaco, Consolas, \"Liberation Mono\", \"Courier New\", monospace !default;\r\n$font-family-base:       $font-family-sans-serif !default;\r\n\r\n$font-size-base: 1rem !default; // Assumes the browser default, typically `16px`\r\n$font-size-lg:   1.25rem !default;\r\n$font-size-sm:   .875rem !default;\r\n$font-size-xs:   .75rem !default;\r\n\r\n$font-weight-normal: normal !default;\r\n$font-weight-bold: bold !default;\r\n\r\n$font-weight-base: $font-weight-normal !default;\r\n$line-height-base: 1.5 !default;\r\n\r\n$font-size-h1: 2.5rem !default;\r\n$font-size-h2: 2rem !default;\r\n$font-size-h3: 1.75rem !default;\r\n$font-size-h4: 1.5rem !default;\r\n$font-size-h5: 1.25rem !default;\r\n$font-size-h6: 1rem !default;\r\n\r\n$headings-margin-bottom: ($spacer / 2) !default;\r\n$headings-font-family:   inherit !default;\r\n$headings-font-weight:   500 !default;\r\n$headings-line-height:   1.1 !default;\r\n$headings-color:         inherit !default;\r\n\r\n$display1-size: 6rem !default;\r\n$display2-size: 5.5rem !default;\r\n$display3-size: 4.5rem !default;\r\n$display4-size: 3.5rem !default;\r\n\r\n$display1-weight:     300 !default;\r\n$display2-weight:     300 !default;\r\n$display3-weight:     300 !default;\r\n$display4-weight:     300 !default;\r\n$display-line-height: $headings-line-height !default;\r\n\r\n$lead-font-size:   1.25rem !default;\r\n$lead-font-weight: 300 !default;\r\n\r\n$small-font-size: 80% !default;\r\n\r\n$text-muted: $gray-light !default;\r\n\r\n$abbr-border-color: $gray-light !default;\r\n\r\n$blockquote-small-color:  $gray-light !default;\r\n$blockquote-font-size:    ($font-size-base * 1.25) !default;\r\n$blockquote-border-color: $gray-lighter !default;\r\n$blockquote-border-width: .25rem !default;\r\n\r\n$hr-border-color: rgba($black,.1) !default;\r\n$hr-border-width: $border-width !default;\r\n\r\n$mark-padding: .2em !default;\r\n\r\n$dt-font-weight: $font-weight-bold !default;\r\n\r\n$kbd-box-shadow:         inset 0 -.1rem 0 rgba($black,.25) !default;\r\n$nested-kbd-font-weight: $font-weight-bold !default;\r\n\r\n$list-inline-padding: 5px !default;\r\n\r\n\r\n// Components\r\n//\r\n// Define common padding and border radius sizes and more.\r\n\r\n$line-height-lg:         (4 / 3) !default;\r\n$line-height-sm:         1.5 !default;\r\n\r\n$border-radius:          .2rem !default;\r\n$border-radius-lg:       .2rem !default;\r\n$border-radius-sm:       .2rem !default;\r\n\r\n$component-active-color: $white !default;\r\n$component-active-bg:    $brand-primary !default;\r\n\r\n$caret-width:            .3em !default;\r\n\r\n$transition-base:        all .2s ease-in-out !default;\r\n$transition-fade:        opacity .15s linear !default;\r\n$transition-collapse:    height .35s ease !default;\r\n\r\n\r\n// Tables\r\n//\r\n// Customizes the `.table` component with basic values, each used across all table variations.\r\n\r\n$table-cell-padding:            .75rem !default;\r\n$table-sm-cell-padding:         .3rem !default;\r\n\r\n$table-bg:                      transparent !default;\r\n\r\n$table-inverse-bg:              $gray-dark !default;\r\n$table-inverse-color:           $body-bg !default;\r\n\r\n$table-bg-accent:               rgba($black,.045) !default;\r\n$table-bg-hover:                rgba($black,.08) !default;\r\n$table-bg-active:               $table-bg-hover !default;\r\n\r\n$table-head-bg:                 $gray-lighter !default;\r\n$table-head-color:              $gray !default;\r\n\r\n$table-border-width:            $border-width !default;\r\n$table-border-color:            rgba($black,.1) !default;\r\n\r\n\r\n// Buttons\r\n//\r\n// For each of Bootstrap's buttons, define text, background and border color.\r\n\r\n$btn-padding-x:                  1rem !default;\r\n$btn-padding-y:                  .5rem !default;\r\n$btn-line-height:                1.25 !default;\r\n$btn-font-weight:                $font-weight-normal !default;\r\n$btn-box-shadow:                 inset 0 1px 0 rgba($white,.15), 0 1px 1px rgba($black,.075) !default;\r\n$btn-focus-box-shadow:           0 0 0 2px rgba($brand-primary, .25) !default;\r\n$btn-active-box-shadow:          inset 0 3px 5px rgba($black,.125) !default;\r\n\r\n$btn-primary-color:              $white !default;\r\n$btn-primary-bg:                 $brand-primary !default;\r\n$btn-primary-border:             $btn-primary-bg !default;\r\n\r\n$btn-secondary-color:            $gray-dark !default;\r\n$btn-secondary-bg:               $white !default;\r\n$btn-secondary-border:           #ccc !default;\r\n\r\n$btn-info-color:                 $white !default;\r\n$btn-info-bg:                    $brand-info !default;\r\n$btn-info-border:                $btn-info-bg !default;\r\n\r\n$btn-success-color:              $white !default;\r\n$btn-success-bg:                 $brand-success !default;\r\n$btn-success-border:             $btn-success-bg !default;\r\n\r\n$btn-warning-color:              $white !default;\r\n$btn-warning-bg:                 $brand-warning !default;\r\n$btn-warning-border:             $btn-warning-bg !default;\r\n\r\n$btn-danger-color:               $white !default;\r\n$btn-danger-bg:                  $brand-danger !default;\r\n$btn-danger-border:              $btn-danger-bg !default;\r\n\r\n$btn-link-disabled-color:        $gray-light !default;\r\n\r\n$btn-padding-x-sm:               .5rem !default;\r\n$btn-padding-y-sm:               0 !default;\r\n\r\n$btn-padding-x-lg:               1.5rem !default;\r\n$btn-padding-y-lg:               .75rem !default;\r\n\r\n$btn-block-spacing-y:            .5rem !default;\r\n$btn-toolbar-margin:             .5rem !default;\r\n\r\n// Allows for customizing button radius independently from global 