ss required by applicable law or agreed to in writing, software\r\n * distributed under the License is distributed on an \"AS IS\" BASIS,\r\n * WITHOUT WARRANTIES OR CONDITIONS OF ANY KIND, either express or implied.\r\n * See the License for the specific language governing permissions and\r\n * limitations under the License.\r\n */\r\n\r\n@import \"../variables\";\r\n@import \"../mixins\";\r\n\r\n.mdl-shadow--2dp {\r\n  @include shadow-2dp();\r\n}\r\n\r\n.mdl-shadow--3dp {\r\n  @include shadow-3dp();\r\n}\r\n\r\n.mdl-shadow--4dp {\r\n  @include shadow-4dp();\r\n}\r\n\r\n.mdl-shadow--6dp {\r\n  @include shadow-6dp();\r\n}\r\n\r\n.mdl-shadow--8dp {\r\n  @include shadow-8dp();\r\n}\r\n\r\n.mdl-shadow--16dp {\r\n  @include shadow-16dp();\r\n}\r\n\r\n.mdl-shadow--24dp {\r\n  @include shadow-24dp();\r\n}\r\n","// overrides mdl\r\naddress {\r\n  line-height: $line-height-base;\r\n}\r\n\r\nh1, h2, h3, h4, h5, h6 {\r\n  line-height: $headings-line-height;\r\n}","\r\n\r\n// \r\n@mixin divider() {\r\n  display: block;\r\n  border: 0;\r\n  border-top: 1px solid transparent;\r\n  &.divider-solid {\r\n    border-style: solid;\r\n    border-color: $state-default-border;\r\n  }\r\n  &.divider-dashed {\r\n    border-style: dashed;\r\n    border-color: $state-default-border;\r\n  }\r\n  &.divider-dotted {\r\n    border-style: dotted;\r\n    border-color: $state-default-border;\r\n  }\r\n}\r\n.divider {\r\n  @include divider();\r\n  margin-top: 10px;\r\n  margin-bottom: 10px;\r\n}\r\n.divider-xs {\r\n  margin-top: 3px; \r\n  margin-bottom: 3px; \r\n}\r\n.divider-sm { \r\n  margin-top: 5px; \r\n  margin-bottom: 5px; \r\n}\r\n.divider-md { \r\n  margin-top: 15px; \r\n  margin-bottom: 15px; \r\n}\r\n.divider-lg { \r\n  margin-top: 20px;\r\n  margin-bottom: 20px;\r\n}\r\n.divider-xl { \r\n  margin-top: 30px;\r\n  margin-bottom: 30px;\r\n}\r\n.divider-xxl { \r\n  margin-top: 50px;\r\n  margin-bottom: 50px;\r\n}\r\n\r\n// \r\n.space {\r\n  display: inline;\r\n  padding: 6px;\r\n}\r\n.space-md { padding: 15px; }\r\n.space-lg { padding: 25px; }\r\n\r\n// \r\nspan.block {\r\n  display: block;\r\n}\r\n.no-margin { margin: 0 !important; }\r\n.no-margin-h {\r\n  margin-left: 0 !important;\r\n  margin-right: 0 !important;\r\n}\r\n.no-margin-top { margin-top: 0 !important; }\r\n.no-margin-bottom { margin-bottom: 0 !important; }\r\n.no-margin-left { margin-left: 0 !important; }\r\n.no-margin-right { margin-right: 0 !important; }\r\n\r\n.no-border {\r\n  border: 0 !important;\r\n}\r\n.no-border-radius {\r\n  border-radius: 0;\r\n}\r\n.no-padding {\r\n  padding: 0 !important;\r\n}\r\n.no-padding-h {\r\n  padding-left: 0 !important;\r\n  padding-right: 0 !important;\r\n}\r\n.no-shadow {\r\n  box-shadow: 0;\r\n}\r\n.full-width {\r\n  width: 100%;\r\n}\r\n.pull-in {\r\n  margin-left: -15px;\r\n  margin-right: -15px;\r\n}\r\n\r\n\r\n// \r\n.margin-b-lg {\r\n  margin-bottom: 30px;\r\n}","// rgba(#000, .035) // #eee\r\n\r\n$image_path:            \"../assets/images/\" !default;\r\n$image_pathdemo:        \"../assets/images-demo/\" !default;\r\n\r\n\r\n$skin:                  $brand-primary;\r\n$skin-light:            #42A5F5 !default; // Blue 400\r\n$dark:                  #333C44 !default; // darken Blue 100 > #343E46\r\n$dark-light:            lighten($dark, 5%) !default;\r\n$bright:                #fafafa !default;\r\n$white:                 #fff !default;\r\n$gray-light2:           #999 !default;\r\n\r\n$page-bg:               #f5f5f5 !default; // background color of .app-page-container\r\n\r\n\r\n$state-default-bg:              rgba(0,0,0, .1) !default; // also: pagination bg\r\n$state-default-border:          darken(adjust-hue($state-default-bg, -10), 5%) !default; \r\n$state-default-border-light:    #f3f3f3 !default; \r\n\r\n//== Typography\r\n$font-weight-thin:      300 !default;\r\n$font-family-condensed: \"Roboto Condensed\", \"Roboto\", \"Helvetica Neue\", Arial, sans-serif !default\r\n\r\n\r\n// UI\r\n$container_fluid_maxwidth:      1400px;\r\n\r\n\r\n// Cards \r\n$card-padding:          20px !default;\r\n$border-radius-card:    2px !default;\r\n\r\n// Grid Variables\r\n$element-top-margin:        ($grid-gutter-width-base/3) !default;\r\n$element-bottom-margin:     ($grid-gutter-width-base*2)/3 !default;\r\n\r\n// Tables\r\n$theme-dark-data-table-header-color: rgba(#fff, 0.54);\r\n\r\n\r\n// box shadow\r\n@mixin z-depth-1 {\r\n  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);\r\n}\r\n@mixin z-depth-1-half {\r\n  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);\r\n}\r\n@mixin z-depth-2 {\r\n  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\r\n}\r\n@mixin z-depth-3 {\r\n  box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);\r\n}\r\n@mixin z-depth-4 {\r\n  box-shadow: 0 16px 28px 0 rgba(0, 0, 0, 0.22), 0 25px 55px 0 rgba(0, 0, 0, 0.21);\r\n}\r\n@mixin z-depth-5 {\r\n  box-shadow: 0 27px 24px 0 rgba(0, 0, 0, 0.2), 0 40px 77px 0 rgba(0, 0, 0, 0.22);\r\n}","\r\n.article-title,\r\n.article-title-style {\r\n  font-size: 24px;\r\n  font-weight: normal;\r\n  line-height: 32px;\r\n  margin: .6em 0;\r\n  &.color-dark {\r\n    color: $body-color;\r\n  }\r\n}\r\n\r\n//\r\n.chapter {\r\n  .article {\r\n    padding-bottom: 30px;\r\n    &:nth-of-type(1) {\r\n      .article-title {\r\n        margin: 0 0 30px;\r\n        border-top: none;\r\n      }\r\n    }\r\n\r\n    &.article-dark {\r\n      background-color: rgba(0,0,0,.015);\r\n    }\r\n\r\n    &.article-bordered {\r\n      border-top: 1px solid rgba(0, 0, 0, 0.117647);\r\n      border-bottom: 1px solid rgba(0, 0, 0, 0.117647);\r\n\r\n      &:last-child {\r\n        border-bottom: 0;\r\n      }\r\n    }\r\n\r\n    &.padding-lg-v {\r\n      padding: 75px 0;\r\n    }\r\n  }\r\n\r\n  .article-title {\r\n    font-size: 24px;\r\n    font-weight: normal;\r\n    line-height: 32px;\r\n    border-top: 1px solid rgba(0, 0, 0, 0.117647);\r\n  }\r\n}\r\n\r\n// Hero\r\n// ------------------------------------\r\n.hero {\r\n  padding: 70px 0 75px;\r\n  text-align: center;\r\n}\r\n.hero-title{\r\n  -webkit-font-smoothing: antialiased;\r\n}\r\n.hero-title {\r\n  color: $body-color;\r\n  font-size: 45px;\r\n  font-weight: $font-weight-thin;\r\n  line-height: 50px;\r\n  margin-bottom: 10px;\r\n}\r\n.hero-tagline {\r\n  margin: 10px auto 30px;\r\n  max-width: 700px;\r\n  color: $body-color;\r\n  font-weight: $font-weight-thin;\r\n  font-size: 24px;\r\n  line-height: 32px;\r\n}\r\n\r\n// Dark theme\r\n// ------------------------------------\r\n.theme-gray,\r\n.theme-dark {\r\n  .hero-title,\r\n  .hero-tagline {\r\n    color: $theme_dark_text_color;\r\n  }\r\n\r\n  .article-title {\r\n    &.color-dark {\r\n      color: $theme_dark_text_color;\r\n    }\r\n  }\r\n}\r\n","// Light background sidebar\r\n// --------------------------------------------------\r\n$light_nav_text_color:                $body-color;\r\n$light_nav_link_hover_bg:             transparent; //#f6f6f6;\r\n$light_nav_link_hover_color:          $brand-primary;\r\n$light_nav_link_active_bg:            transparent;\r\n$light_nav_link_active_color:         $brand-primary;\r\n$light_nav_link_open_bg:              rgba(0,0,0,.05);      // vertical nav only\r\n$light_nav_link_open_color:           $light_nav_link_active_color;   // vertical nav only\r\n\r\n$light_nav_sub_nav_bg:                $light_nav_link_open_bg;\r\n$light_nav_sub_link_active_color:     $brand-primary;\r\n// $light_nav_sub2_nav_bg:               #eaeaea;\r\n$light_nav_sub2_nav_bg:               rgba(0,0,0,.05);\r\n// $light_nav_sub3_nav_bg:               #ddd;\r\n$light_nav_sub3_nav_bg:               rgba(0,0,0,.05);\r\n\r\n\r\n\r\n// Themes\r\n// --------------------------------------------------\r\n$theme_dark_text_color:       rgba(255,255,255,.7);\r\n$theme_dark_text_color_light: rgba(255,255,255,.25);\r\n\r\n$theme_dark_sidebar_bg_color: $dark;\r\n$theme_dark_header_bg_color:  lighten($dark, 6%);\r\n$theme_dark_page_bg:          lighten($dark, 2.5%);\r\n$theme_dark_box_bg:           $theme_dark_header_bg_color;\r\n\r\n$theme_gray_sidebar_bg_color: grayscale($theme_dark_sidebar_bg_color); // or 3c3c3c\r\n$theme_gray_header_bg_color:  grayscale($theme_dark_header_bg_color); // or #4c4c4c\r\n$theme_gray_page_bg:          grayscale($theme_dark_page_bg); // #444;\r\n$theme_gray_text_color:       $theme_dark_text_color;\r\n$theme_gray_box_bg:           $theme_gray_header_bg_color;\r\n",".container-fluid {\r\n  padding-left: 30px;\r\n  padding-right: 30px;\r\n  &.padding-lg-h {\r\n    padding-left: $grid-gutter-width-base;\r\n    padding-right: $grid-gutter-width-base;\r\n  }\r\n  &.with-maxwidth {\r\n    max-width: $container_fluid_maxwidth;\r\n  }\r\n  &.with-maxwidth-lg {\r\n    max-width: map-get($container-max-widths, lg);\r\n  }\r\n  &.with-maxwidth-md {\r\n    max-width: map-get($container-max-widths, lg);\r\n  }\r\n  &.with-maxwidth-sm {\r\n    max-width: map-get($container-max-widths, md);\r\n  }\r\n  &.no-breadcrumbs {\r\n    padding-top: 2em;\r\n    padding-bottom: 1em;\r\n  }\r\n}","\r\n// Including: Gradient text, Dropcap, Highlight, List, divider/space, background\r\n\r\n\r\n// Gradient text\r\n.gradient-text {\r\n  font-size: 150px;\r\n  font-weight: 300;\r\n  color: $brand-primary; // fallback\r\n  background: -webkit-linear-gradient(92deg, #fb83fa, #00aced);\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n}\r\n\r\n.text-small { font-size: 12px; }\r\n.text-normal { font-size: $font-size-base; }\r\n.text-huge { font-size: 72px; }\r\n.text-large { font-size: 50px; }\r\n.text-thin { font-weight: 300; }\r\n.text-ultralight { font-weight: 100; }\r\n\r\n.bold {\r\n  font-weight: $headings-font-weight;\r\n}\r\n\r\n.color-primary,\r\na.color-primary { \r\n  color: $brand-primary;\r\n  &:hover { color: $brand-primary; }\r\n}\r\n.color-success,\r\na.color-success { \r\n  color: $brand-success;\r\n  &:hover { color: $brand-success; }\r\n}\r\n.color-info,\r\na.color-info { \r\n  color: $brand-info;\r\n  &:hover { color: $brand-info; }\r\n}\r\n.color-info-alt,\r\na.color-info-alt { \r\n  color: $brand-info-alt;\r\n  &:hover { color: $brand-info-alt; }\r\n}\r\n.color-warning,\r\na.color-warning { \r\n  color: $brand-warning;\r\n  &:hover { color: $brand-warning; }\r\n}\r\n.color-danger,\r\na.color-danger { \r\n  color: $brand-danger;\r\n  &:hover { color: $brand-danger; }\r\n}\r\n.color-dark,\r\na.color-dark { \r\n  color: $dark;\r\n  &:hover { color: $dark; }\r\n}\r\n.color-white,\r\na.color-white { \r\n  color: $white;\r\n  &:hover { color: $white; }\r\n}\r\n.color-gray-darker,\r\na.color-gray-darker { \r\n  color: $gray-darker;\r\n  &:hover { color: $gray-darker; }\r\n}\r\n.color-gray-dark,\r\na.color-gray-dark { \r\n  color: $gray-dark;\r\n  &:hover { color: $gray-dark; }\r\n}\r\n.color-gray,\r\na.color-gray { \r\n  color: $gray;\r\n  &:hover { color: $gray; }\r\n}\r\n.color-gray-light,\r\na.color-gray-light { \r\n  color: $gray-light;\r\n  &:hover { color: $gray-light; }\r\n}\r\n.color-gray-lighter,\r\na.color-gray-lighter { \r\n  color: $gray-lighter;\r\n  &:hover { color: $gray-lighter; }\r\n}\r\n\r\n\r\n// Dropcap\r\n.dropcap,\r\n.dropcap-square,\r\n.dropcap-circle {\r\n  display: block;\r\n  float: left;\r\n  font-weight: normal;\r\n  line-height: 36px;\r\n  margin-right: 6px;\r\n  text-shadow: none;    \r\n}\r\n\r\n.dropcap {\r\n  font-size: 3.1em;\r\n}\r\n\r\n.dropcap-square,\r\n.dropcap-circle {\r\n  background-color: $gray-lighter;\r\n  color: $body-color;\r\n  width: 36px;\r\n  text-align: center;\r\n}\r\n\r\n.dropcap-square {\r\n  border-radius: $border-radius;    \r\n  font-size: 2.3em;\r\n}\r\n\r\n.dropcap-circle {\r\n  border-radius: 50%;    \r\n  font-size: 1.78em;\r\n}\r\n\r\n.dropcap.colored {\r\n  color: $brand-primary;\r\n}\r\n\r\n.dropcap-square.colored,\r\n.dropcap-circle.colored {\r\n  background-color: $brand-primary;\r\n  color: $white;\r\n}\r\n\r\n\r\n// Hightlight\r\n.ui-highlight {\r\n  background-color: $dark;\r\n  color: $white;\r\n  border-radius: $border-radius;    \r\n  padding: 2px 5px;\r\n  &.colored {\r\n    background-color: $brand-primary;\r\n  }\r\n}\r\n\r\n\r\n\r\n","\r\n// mixin\r\n@mixin button-icon-variant($size) {\r\n  height: $size;\r\n  width: $size;\r\n  line-height: $size;\r\n  i { line-height: $size; }\r\n  &.btn-icon-lined {\r\n    line-height: ($size - 4px);\r\n    i { line-height: ($size - 4px); }\r\n\r\n    &.btn-icon-thin {\r\n      line-height: ($size - 2px);\r\n      i { line-height: ($size - 2px); }\r\n    }\r\n  }\r\n}\r\n\r\n@mixin social-button-variant($background, $border) {\r\n  color: $white;\r\n  background-color: $background;\r\n  border-color: $border;\r\n  &:hover,\r\n  &:focus,\r\n  &:active {\r\n    color: $white;\r\n    text-decoration: none;\r\n    background-color: darken($background, 8%);\r\n    border-color: darken($border, 12%)\r\n  }\r\n}\r\n\r\n\r\n\r\n// make sure it overrides material2 styles\r\na,\r\nbutton {\r\n  &.btn-w-xs { min-width: 80px; }\r\n  &.btn-w-sm { min-width: 100px; }\r\n  &.btn-w-md { min-width: 135px; }\r\n  &.btn-w-lg { min-width: 160px; }\r\n\r\n  &.btn-round { border-radius: 2em; }\r\n  &.btn-gap { margin: 5px; }\r\n  &.btn-gap-h { margin: 0 5px; }\r\n  &.btn-gap-v { margin: 0 0 5px; }\r\n}\r\n\r\n\r\n//\r\n.btn-icon {\r\n  @include button-icon-variant(35px);\r\n  padding: 0;\r\n  display: inline-block;\r\n  text-align: center;\r\n  border-radius: $border-radius;\r\n}\r\n.btn-icon-round {\r\n  border-radius: 50%;\r\n}\r\n.btn-icon-sm {\r\n  @include button-icon-variant(30px);\r\n}\r\n.btn-icon-md {\r\n  @include button-icon-variant(45px);\r\n  font-size: 18px;\r\n}\r\n.btn-icon-lg {\r\n  @include button-icon-variant(65px);\r\n  font-size: 28px;\r\n}\r\n.btn-icon-lg-alt {\r\n  @include button-icon-variant(70px);\r\n}\r\n.btn-icon-xl {\r\n  @include button-icon-variant(80px);\r\n}\r\n\r\n\r\n// social buttons\r\n.btn-twitter { @include social-button-variant(#00c7f7, #00c7f7); }\r\n.btn-facebook { @include social-button-variant(#335397, #335397); }\r\n.btn-gplus,\r\n.btn-google-plus { @include social-button-variant(#dd4a38, #dd4a38); }\r\n.btn-instagram { @include social-button-variant(#82685A, #82685A); }\r\n.btn-vimeo { @include social-button-variant(#63879C, #63879C); }\r\n.btn-flickr { @include social-button-variant(#0061DB, #0061DB); }\r\n.btn-github { @include social-button-variant(#3B3B3B, #3B3B3B); }\r\n.btn-pinterest { @include social-button-variant(#D73532, #D73532); }\r\n.btn-tumblr { @include social-button-variant(#586980, #586980); }\r\n.btn-linkedin { @include social-button-variant(#018FAF, #018FAF); }\r\n.btn-dribbble { @include social-button-variant(#EA73A0, #EA73A0); }\r\n.btn-stumbleupon { @include social-button-variant(#EA4B24, #EA4B24); }\r\n.btn-lastfm { @include social-button-variant(#B80638, #B80638); }\r\n.btn-evernote { @include social-button-variant(#3BAB27, #3BAB27); }\r\n.btn-skype { @include social-button-variant(#00B0F6, #00B0F6); }\r\n.btn-soundcloud { @include social-button-variant(#0066FF, #0066FF); }\r\n.btn-behance { @include social-button-variant(#B80638, #B80638); }\r\n.btn-rss { @include social-button-variant(#F79638, #F79638); }\r\n.btn-youtube { @include social-button-variant(#CC181E, #CC181E); }\r\n\r\n\r\n\r\n// Dark theme\r\n// --------------------------------------------------\r\n.theme-gray,\r\n.theme-dark {\r\n  .btn-default,\r\n  .btn-secondary {\r\n    background-color: rgba(#fff, .05);\r\n    border-color: rgba(#000, .15);\r\n    color: $theme_dark_text_color;\r\n  }\r\n}\r\n","\r\n\r\n// flip scroll (Pure CSS), thanks to http://elvery.net/demo/responsive-tables/#flip-scroll\r\n@media only screen and (max-width: 800px) {\r\n  .table-flip-scroll {\r\n    .cf:after { visibility: hidden; display: block; font-size: 0; content: \" \"; clear: both; height: 0; }\r\n    * html .cf { zoom: 1; }\r\n    *:first-child+html .cf { zoom: 1; }\r\n    table { width: 100%; border-collapse: collapse; border-spacing: 0; }\r\n\r\n    th,\r\n    td { margin: 0; vertical-align: top; }\r\n    th { text-align: left; }\r\n    table { display: block; position: relative; width: 100%; }\r\n    thead { display: block; float: left; }\r\n    tbody { display: block; width: auto; position: relative; overflow-x: auto; white-space: nowrap; }\r\n    thead tr { display: block; }\r\n    .table > thead > tr > th:first-child { // add to Bootstrap style\r\n      border-top: 1px solid #ddd;\r\n    }\r\n    th { display: block; text-align: right; }\r\n    tbody tr { display: inline-block; vertical-align: top; }\r\n    td { display: block; min-height: 1.25em; text-align: left; }\r\n\r\n\r\n    /* sort out borders */\r\n\r\n    th { border-bottom: 0; border-left: 0; }\r\n    td { border-left: 0; border-right: 0; border-bottom: 0; }\r\n    tbody tr { border-left: 1px solid #babcbf; }\r\n    th:last-child,\r\n    td:last-child { border-bottom: 1px solid #babcbf; }\r\n\r\n    // overrides md-data-table\r\n    .mdl-data-table tbody tr {\r\n      height: auto;\r\n    }\r\n  }\r\n}\r\n","// Dark theme\r\n// --------------------------------------------------\r\n.theme-gray,\r\n.theme-dark {\r\n  .form-control,\r\n  .input-group-addon{\r\n    background-color: rgba(#fff, .05);\r\n    border-color: rgba(#000, .15);\r\n    color: $theme_dark_text_color;\r\n  }\r\n  .help-block {\r\n    color: $gray-light;\r\n  }\r\n}",".input-round {\r\n  border-radius: 2em;\r\n}\r\n\r\ninput.input-primary {\r\n  border-color: $skin;\r\n  &:focus {\r\n    border-color: $skin;\r\n  }\r\n}\r\ninput.input-info {\r\n  border-color: $brand-info;\r\n  &:focus {\r\n    border-color: $brand-info;\r\n  }\r\n}\r\ninput.input-success {\r\n  border-color: $brand-success;\r\n  &:focus {\r\n    border-color: $brand-success;\r\n  }\r\n}\r\ninput.input-warning {\r\n  border-color: $brand-warning;\r\n  &:focus {\r\n    border-color: $brand-warning;\r\n  }\r\n}\r\ninput.input-danger {\r\n  border-color: $brand-danger;\r\n  &:focus {\r\n    border-color: $brand-danger;\r\n  }\r\n}\r\n",".box {\r\n  position: relative;\r\n  margin-bottom: 1.5rem;\r\n  border: 0;\r\n  border-radius: $border-radius;\r\n  box-shadow: 0 0 2px rgba(0,0,0,.15);\r\n\r\n  &.box-transparent {\r\n    box-shadow: none;\r\n  }\r\n\r\n  .box-dark {\r\n    background-color: rgba(0,0,0,.03);\r\n  }\r\n}\r\n\r\n// put color in .box-default, so .bg-xxx can overrides box color easily\r\n.box-default {\r\n  //background-color: lighten($dark, 5%);\r\n}\r\n\r\n.box-header,\r\n.box-heading {\r\n  padding: $card-spacer-y $card-spacer-x;\r\n  border-bottom: 1px solid transparent;\r\n  border-radius: ($border-radius - 1) ($border-radius - 1) 0 0;\r\n  font-weight: $headings-font-weight;\r\n  // text-transform: uppercase;\r\n}\r\n\r\n.box-toolbar {\r\n  position: absolute;\r\n  top: $card-spacer-x;\r\n  right: $card-spacer-x;\r\n\r\n  a {\r\n    color: $text-muted;\r\n\r\n    .material-icons {\r\n      font-size: $font-size-base;\r\n    }\r\n  }\r\n}\r\n\r\n.box-divider {\r\n  height: 1px;\r\n  background: rgba(0,0,0,0.08);\r\n  margin: 0 $card-spacer-x;\r\n}\r\n\r\n.box-body {\r\n  @include clearfix;\r\n  padding: $card-spacer-x;\r\n\r\n  &.padding-lg {\r\n    padding: 30px 15px;\r\n  }\r\n  &.padding-xl {\r\n    padding: 60px 15px;\r\n  }\r\n  &.padding-lg-h {\r\n    padding: 15px 30px;\r\n  }\r\n  &.padding-lg-v {\r\n    padding: 30px 15px;\r\n  }\r\n}\r\n\r\n\r\n// Theme\r\n// --------------------------------------------------\r\n.theme-gray {\r\n  .box-default {\r\n    background-color: $theme_gray_box_bg;\r\n  }\r\n}\r\n.theme-dark {\r\n  .box-default {\r\n    background-color: $theme_dark_box_bg;\r\n  }\r\n}\r\n","@mixin clearfix() {\r\n  &::after {\r\n    display: block;\r\n    content: \"\";\r\n    clear: both;\r\n  }\r\n}\r\n",".breadcrumb {\r\n  display: block;\r\n  margin: 0;\r\n  border: 0;\r\n  border-radius: 0;\r\n  box-shadow: none;\r\n  background-color: transparent;\r\n  padding: 15px 0;\r\n  text-transform: uppercase;\r\n  color: #ccc;\r\n  text-shadow: none;\r\n  font-size: 11px;\r\n\r\n  a {\r\n    color: #ccc;\r\n    text-decoration: none;\r\n    &:hover {\r\n      color: $link-color;\r\n    }\r\n  }\r\n\r\n  li {\r\n    padding-left: 0;\r\n  }\r\n\r\n}\r\n",".call-to-action {\r\n  position: relative;\r\n  text-align: center;\r\n  .cta-inner {\r\n    padding: 2em 0;\r\n  }\r\n  .cta-text {\r\n    font-size: 24px;\r\n    line-height: 30px;\r\n  }\r\n  .cta-btn {\r\n    margin: 30px 0 10px;\r\n  }\r\n  .cta-muted {\r\n    opacity: .7;\r\n  }\r\n  &.cta-inline {\r\n    .cta-text {\r\n      font-size: 20px;\r\n      // line-height: 30px;\r\n      font-weight: $font-weight-thin;\r\n    }\r\n\r\n    .cta-btn {\r\n      display: inline-block;\r\n      margin: 0 0 0 30px;\r\n    }\r\n  }\r\n\r\n  &.cta-full-width {\r\n    .cta-inner {\r\n      padding: 6em 1em;\r\n    }\r\n  }\r\n\r\n  &.cta-bg-img {\r\n    background-size: cover;\r\n    background-position: center;\r\n    color: rgba(#fff, .8);\r\n    .cta-inner {\r\n      background-color: rgba(#000, .35);\r\n      padding: 7em 1em;\r\n    }\r\n  }\r\n}\r\n\r\n",".callout {\r\n  margin: 20px 0;\r\n  padding: 20px;\r\n  border-left: 3px solid #eee;\r\n\r\n  h4 {\r\n    margin-top: 0;\r\n    margin-bottom: 5px;\r\n    font-size: $font-size-lg;\r\n    line-height: $line-height-base;\r\n  }\r\n\r\n  p:last-child {\r\n    margin-bottom: 0;\r\n  }\r\n}\r\n\r\n// use rgba so that background-color also works for dark theme\r\n.callout-success {\r\n  background-color: rgba($brand-success, .08); // #f3faf3\r\n  border-color: $brand-success;\r\n\r\n  h4 {\r\n    color: $brand-success;\r\n  }   \r\n}\r\n\r\n.callout-info {\r\n  background-color: rgba($brand-info, .05); // #f4f8fa\r\n  border-color: $brand-info;\r\n\r\n  h4 {\r\n    color: $brand-info;\r\n  }\r\n}\r\n\r\n.callout-warning {\r\n  background-color: rgba($brand-warning, .08); // #fcf8f2\r\n  border-color: $brand-warning;\r\n\r\n  h4 {\r\n    color: $brand-warning;\r\n  }\r\n}\r\n\r\n.callout-danger {\r\n  background-color: rgba($brand-danger, .05); // #fdf7f7\r\n  border-color: $brand-danger;\r\n\r\n  h4 {\r\n    color: $brand-danger;\r\n  }\r\n}\r\n\r\n\r\n",".card-white {\r\n  color: $body-color;\r\n  background-color: #fff;\r\n}\r\n\r\n\r\n\r\n// \r\na.item-card {\r\n  display: block;\r\n  text-decoration: none;\r\n  &:hover {\r\n    box-shadow: 0 0 15px rgba(0,0,0, .1);\r\n  }\r\n}\r\n.item-card {\r\n  position: relative;\r\n  transition: 0.2s linear;\r\n\r\n  img {\r\n    max-width: 100%;\r\n    max-height: 100%;\r\n  }\r\n  .card__image {\r\n    padding: 30px 15px;\r\n    height: 300px;\r\n    background-color: rgba(#000, .035);\r\n    text-align: center;\r\n  }\r\n\r\n  h1, h2, h3, h4, h5, h6 {\r\n    margin: 0;\r\n    color: $body-color;\r\n    line-height: $line-height-base;\r\n  }\r\n  h6 {\r\n    font-size: 10px;\r\n    opacity: .5;\r\n  }\r\n  h4 {\r\n    font-size: 16px;\r\n  }\r\n\r\n  .card__body {\r\n    position: relative;\r\n    padding: 30px 15px;\r\n    margin-bottom: 30px;\r\n  }\r\n\r\n  .card__price {\r\n    position: absolute;\r\n    top: 30px;\r\n    right: 15px;\r\n    span {\r\n      display: block;\r\n      color: $gray-light;\r\n      &.type--strikethrough {\r\n        opacity: .5;\r\n        text-decoration: line-through;\r\n      }\r\n    }\r\n  }\r\n\r\n  &.card__horizontal {\r\n    position: relative;\r\n    margin-bottom: 30px;\r\n\r\n    .card__image {\r\n      width: 50%;\r\n    }\r\n\r\n    .card__body {\r\n      width: 50%;\r\n      height: 100%;\r\n      position: absolute;\r\n      top: 0;\r\n      right: 0;\r\n      padding: 40px 30px;\r\n    }\r\n    .card__price {\r\n      top: 30px;\r\n      right: 30px;\r\n    }\r\n\r\n    .card__desc {\r\n      margin: 0 25% 3em 0;\r\n      opacity: .7;\r\n    }\r\n  }\r\n}\r\n\r\n\r\n// Dark theme\r\n.theme-gray,\r\n.theme-dark {\r\n  .card-white {\r\n    color: $theme_dark_text_color;\r\n    h1, h2, h3, h4, h5, h6 {\r\n      color: $theme_dark_text_color;\r\n    }\r\n  }\r\n}\r\n.theme-gray {\r\n  .card-white {\r\n    background-color: $theme_gray_box_bg;\r\n  }\r\n}\r\n.theme-dark {\r\n  .card-white {\r\n    background-color: $theme_dark_box_bg;\r\n  }\r\n}\r\n",".feature-callout {\r\n  position: relative;\r\n  clear: both;\r\n\r\n  &.feature-content-left {\r\n    .callout-feature-content {\r\n      @media (min-width: map-get($grid-breakpoints, md)) { padding-right: 2em; }\r\n      @media (min-width: map-get($grid-breakpoints, xl)) { padding-right: 3em; }\r\n    }\r\n  }\r\n  &.feature-content-right {\r\n    .callout-feature-content {\r\n      @media (min-width: map-get($grid-breakpoints, md)) { padding-left: 2em; }\r\n      @media (min-width: map-get($grid-breakpoints, xl)) { padding-left: 3em; }\r\n    }\r\n  }\r\n\r\n  &.feature-content-center {\r\n    .callout-feature-content {\r\n      text-align: center;\r\n      max-width: 75%;\r\n      margin: 0 auto;\r\n    }\r\n  }\r\n\r\n  .md-button {\r\n    padding: 5px 0;\r\n    min-width: 150px;\r\n  }\r\n}\r\n\r\n.feature-callout-cover,\r\n.feature-callout-image-pull {\r\n  background-size: cover;\r\n  background-position: center;\r\n}\r\n\r\n.feature-callout-image-pull {\r\n  min-height: 200px;\r\n  @media (min-width: map-get($grid-breakpoints, md)) {\r\n    position:absolute;\r\n    top: 0;\r\n    bottom: 0\r\n  }\r\n}\r\n\r\n.callout-feature-content {\r\n  padding: 3em 0;\r\n  @media (min-width: map-get($grid-breakpoints, md)) {\r\n    padding-top: 6em;\r\n    padding-bottom: 6em;\r\n  }\r\n  @media (min-width: 992px) {\r\n    padding-top: 8em;\r\n    padding-bottom: 8em;\r\n  }\r\n  @media (min-width: map-get($grid-breakpoints, xl)) {\r\n    padding-top: 12em;\r\n    padding-bottom: 12em;\r\n  }\r\n\r\n  h2 {\r\n    font-size: 24px;\r\n    margin-top: 0;\r\n    @media (min-width: map-get($grid-breakpoints, md)) {\r\n      font-size: 30px;\r\n      font-weight: 400;\r\n      line-height: 1.9;\r\n      margin: 0;\r\n    }\r\n  }\r\n  p {\r\n    line-height: 1.75;\